.Titulo-Soli {
    width: 100%;
    font-size: 50px;
    text-align: center;
}

.titulo-ver-soli {
    width: 100%;
    font-size: 50px;
    text-align: center;
}

.titulos-soli {
    font-size: 20px;
    text-align: center;
}

.caja-grupo-soli {
    width: 10%;
}

.caja-sis-soli {
    width: 20%;
}

.caja-materia-soli {
    width: 30%;
    height: 60%;
    overflow: auto;
}

.caja-btns-soli {
    width: 90%;
    display: flex;
    justify-content: space-evenly;
}


.editar-soli {
    width: 100%;
    margin: 5px;
    color: #fff;
}


.btn-ver-soli {
    background-color: #4fa8fb;
    transition: .6s;
}

.btn-ver-mat:hover {
    background-color: rgba(79, 168, 251, .5);
}

table tr th,
table tr td {
    padding: 10px;
}

.td-btns-soli {
    width: 180px;
}

table {
    border: solid 1px #c8c8c8;
    box-shadow: 5px 5px 12px #c8c8c8;
    margin: 20px;
}

table tr:nth-child(odd) td {
    background-color: #ffffff;
}

.titulo-tabla-soli {
    background-color: rgba(33, 37, 41, .9);
    color: #ffffff;
}

.contenedor-tabla-soli {
    display: flex;
    justify-content: center;
}

.editar-solii{
  background-color: rgb(10, 248, 62);
  margin: 5%;
  padding: 1px6px;
}

/* modificacion de estilos de iconos */
.btns-solicitudes {
    background: transparent;
    border: none;
    font-size: 25px;
    width: 40%;
}

.caja-btns-soli-admin {
    width: 90%;
    display: flex;
    flex-direction: row;
}

.bi-clipboard2-plus-fill {
    color: #00AB66;
}

.parraf-solicitudes-vacio {
    color: #000;
}